/* Global Styling */


/* unvisited link */
a {
  color:black;
  text-decoration:none;
}
a:link {
  color:black;
  text-decoration:none;
}

/* visited link */
a:visited {
  color:black;
  text-decoration:none;
}

/* mouse over link */
a:hover {
  color:#6ed3cf;
  text-decoration:none;
}

/* selected link */
a:active {
  color: darkgrey;
}
